#progressbar {
  margin-bottom: 60px;
  overflow: hidden;
  /*CSS counters to number the steps*/
  counter-reset: step;
}

/* line 41, src/assets/scss/pages/_crearllamado.scss */
#progressbar li {
  text-align: center;
  list-style-type: none;
  color: white;
  text-transform: uppercase;
  position: relative;
}

/* line 48, src/assets/scss/pages/_crearllamado.scss */
#progressbar li:before {
  content: counter(step);
  counter-increment: step;
  width: 20px;
  line-height: 20px;
  display: block;
  font-size: 10px;
  color: #333;
  background: white;
  border-radius: 3px;
  margin: 0 auto 5px auto;
}

/*progressbar connectors*/
/* line 61, src/assets/scss/pages/_crearllamado.scss */
#progressbar li:after {
  content: '';
  width: 100%;
  height: 2px;
  background: white;
  position: absolute;
  left: -50%;
  top: 9px;
  z-index: -1;
  /*put it behind the numbers*/
}

/* line 71, src/assets/scss/pages/_crearllamado.scss */
#progressbar li:first-child:after {
  /*connector not needed before the first step*/
  content: none;
}

/*marking active/completed steps green*/
/*The number of the step and the connector before it = green*/
/* line 77, src/assets/scss/pages/_crearllamado.scss */
#progressbar li.active:before, #progressbar li.active:after {
  background: #54698D;
  color: white;
}

